import { Typography } from "@material-ui/core";
import React, { Component } from "react";
import { connect } from "react-redux";
import { getData, postData } from "../../actions/action";
import {
  FETCH_APPROVED_USERS,
  FETCH_CLEANING_SCHEDULE_FOR_USER,
  FETCH_FULL_CLEANING_SCHEDULE
} from "../../constants/types.constants";
import {
  createUrl,
  GET_APPROVED_USERS_URL,
  GET_FULL_SCHEDULE_URL,
  GET_SCHEDULE_BY_USER_ID_URL,
  UPDATE_SCHEDULE_URL
} from "../../constants/url.constants";
import { ACCESS_CLEANING_KEY } from "../../constants/user.constants";
import { isAccessible } from "../../helpers/visibility.helper";
import EditableList from "../admin/cleaning/list/EditableList";
import GeneratedList from "../admin/cleaning/list/GeneratedList";
import PermissionDenied from "../common/PermissionDenied";
import SimpleExpansionPanel from "../food/SimpleExpansionPanel";

class Cleaning extends Component {
  componentDidMount() {
    this.props.fetchSchedule(this.props.profileObj.id);
    this.props.fetchFullSchedule();
    this.props.fetchApprovedUsers();
  }

  render() {
    const {
      profileObj,
      userCleaningSchedule,
      fullCleaningSchedule,
      approvedUsers
    } = this.props;
    return isAccessible(profileObj, ACCESS_CLEANING_KEY) ? (
      <div>
        <Typography paragraph>Cleaning</Typography>
        <SimpleExpansionPanel {...this.props}>
          <GeneratedList
            isStyled={true}
            panelClassName="no-padding-expansion-panel"
            panelHeader="Your Schedule"
            generatedList={userCleaningSchedule}
          />
          <EditableList
            approvedUsers={approvedUsers}
            panelClassName="no-padding-expansion-panel"
            panelHeader="Full Schedule"
            generatedList={fullCleaningSchedule}
            isEditable={true}
            placeholder="Search a User"
          />
        </SimpleExpansionPanel>
      </div>
    ) : (
      <PermissionDenied profileObj={profileObj} />
    );
  }
}

Cleaning.propTypes = {};

function mapStateToProps(state, ownProps) {
  return {
    approvedUsers: state.adminReducer.approvedUsers,
    userCleaningSchedule: state.cleaningReducer.userCleaningSchedule,
    fullCleaningSchedule: state.cleaningReducer.fullCleaningSchedule
  };
}

function mapDispatchToProps(dispatch) {
  return {
    fetchApprovedUsers: () =>
      dispatch(
        getData(createUrl(GET_APPROVED_USERS_URL), FETCH_APPROVED_USERS)
      ),
    fetchSchedule: userId =>
      dispatch(
        postData(
          createUrl(GET_SCHEDULE_BY_USER_ID_URL),
          { id: userId },
          FETCH_CLEANING_SCHEDULE_FOR_USER
        )
      ),
    fetchFullSchedule: () =>
      dispatch(
        getData(createUrl(GET_FULL_SCHEDULE_URL), FETCH_FULL_CLEANING_SCHEDULE)
      ),
    updateSchedule: cleaningObj =>
      dispatch(postData(createUrl(UPDATE_SCHEDULE_URL), cleaningObj))
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Cleaning);
